import { mapState } from "vuex";
import { getProfile } from "@/api/profile";
import { getArticles } from "@/api/article";
export default {
  name: "Profile",
  middleware: ["auth"],
  computed: {
    ...mapState(["user"])
  },
  async asyncData({ route }) {
    const { params } = route;
    const { username } = params;
    const [{ profile }, { articles }] = await Promise.all([
      getProfile(username),
      getArticles({
        limit: 10,
        author: username
      })
    ]);

    return {
      profile,
      articles
    };
  }
};
